input.header-toggle-input {
    display: none;
}

.header-toggle {
    display: inline-block;

    > label {
        user-select: none;
    }

    transition: color 200ms, background-color 200ms;
}

.header-toggle--menu .header-toggle__icon {
    @extend .fa !optional;
    @extend .fa-bars !optional;
}

.header-toggle--search .header-toggle__icon {
    @extend .fa !optional;
    @extend .fa-search !optional;
}

.header-toggle__content {
    display: none;
}

input[type="checkbox"]:checked + .header-toggle {
    background-color: black;
    color: white;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;

    z-index: 1;

    .header-toggle__icon {
        @extend .fa-close !optional;
    }

    .header-toggle__content {
        display: block;
    }
}
